@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@400;500;600;700;800&display=swap);
body {
    margin: 10px 0;
    padding: 0px;
    background-color: #3C4048;
    background-image: url('https://wallpaperaccess.com/full/1145359.png');
    font-family: 'Montserrat', sans-serif;
}
.App{
    display: flex;
    justify-content: center;
    height: 100vh;
    align-items: center;
    padding: 15px 0;
    align-items: flex-start;
}
.canvas{
    background-color: #000;
    color: #EAEAEA;
    width: 80vw;
    height: -webkit-fit-content;
    height: -moz-fit-content;
    height: fit-content;
    border-radius: 20px;
    border: solid 1px;
    margin: 20px 0;
    padding-bottom: 15px;
}
.top-row{
    display: flex;
    align-items: center;
    justify-content: space-between;
}
.title-text{
    padding-left: 15px;
}
.book-detail{
    background-color: #e2e2e2;
    display: flex;
    padding: 10px 40px 10px 40px;
    /* padding-left: 50px; */
    /* padding-right: 50px; */
    justify-content: flex-start;
    margin: 10px 0;
    position: relative;
}
.text-details{
    display: flex;
    color: #2e2e2e;
    align-items: flex-start;
    flex-direction: column;
    height: 300px;
    padding: 0 15px;
}
.text-details > p{
    margin: 0;
}
.book-name{
    font-weight: 700;
}
.btm-row{
    display: flex;
    justify-content: space-between;
    width: 100%;
    align-items: center;
}
.dataInputForm {
    margin: 40px 25px 0 0;
}

.dataInputs {
    margin: 15px 25px 0 0;
}
.buttons {
    position: absolute;
    bottom: 20px;
    right: 20px;
}
textArea {
    height: 200px;
}

/* CSS */
.button-54 {
  font-family: "Open Sans", sans-serif;
  font-size: 16px;
  letter-spacing: 2px;
  text-decoration: none;
  text-transform: uppercase;
  color: #bd0000;
  cursor: pointer;
  border: 3px solid;
  padding: 0.25em 0.5em;
  box-shadow: 1px 1px 0px 0px, 2px 2px 0px 0px, 3px 3px 0px 0px, 4px 4px 0px 0px, 5px 5px 0px 0px;
  position: relative;
  user-select: none;
  -webkit-user-select: none;
  touch-action: manipulation;
  margin-left: 15px;
  height: 40px;
  width: 100px;
  z-index: 0;
}
.update{
    color: #00494c;
}
.Add{
    color: #666666;
    margin-right: 20px;
}
.button-54:active {
  box-shadow: 0px 0px 0px 0px;
  top: 5px;
  left: 5px;
}

@media (min-width: 768px) {
  .button-54 {
    padding: 0.25em 0.75em;
  }
}
label {
    width: 100%;
    max-width: 300px;
}

input,
textarea {
    width: 100%;
    display: flex;
    padding: 15px;
    background-color: white;
    border: 2px solid black;
    color: rgb(0, 0, 0);
    font-size: 15px;
    box-shadow: 3px 1px 0px 2px, 0px -1px 0px 2px rgba(0, 0, 0, 0.62);
    height: 20px;
    -webkit-transform: skew(-3deg, 1deg);
            transform: skew(-3deg, 1deg);
    font-family: "Knewave", cursive;
    letter-spacing: 2px;
    text-transform: uppercase;

}

input[type=search]::-webkit-search-cancel-button {
    -webkit-appearance: searchfield-cancel-button;
}

input:focus,
input:valid {
    outline: none;
}

input:focus~ul li:nth-child(1)::before,
input:valid~ul li:nth-child(1)::before {
    -webkit-filter: blur(20px);
            filter: blur(20px);
    -webkit-transform: translate(0, -35px) rotate(-210deg);
            transform: translate(0, -35px) rotate(-210deg);
}

input:focus~ul li:nth-child(1)::after,
input:valid~ul li:nth-child(1)::after {
    -webkit-filter: blur(0);
            filter: blur(0);
    -webkit-transform: scale(1) rotate(0);
            transform: scale(1) rotate(0);
}

input:focus~ul li:nth-child(2)::before,
input:valid~ul li:nth-child(2)::before {
    -webkit-filter: blur(20px);
            filter: blur(20px);
    -webkit-transform: translate(0, -35px) rotate(-210deg);
            transform: translate(0, -35px) rotate(-210deg);
}

input:focus~ul li:nth-child(2)::after,
input:valid~ul li:nth-child(2)::after {
    -webkit-filter: blur(0);
            filter: blur(0);
    -webkit-transform: scale(1) rotate(0);
            transform: scale(1) rotate(0);
}

input:focus~ul li:nth-child(3)::before,
input:valid~ul li:nth-child(3)::before {
    -webkit-filter: blur(20px);
            filter: blur(20px);
    -webkit-transform: translate(0, -35px) rotate(-210deg);
            transform: translate(0, -35px) rotate(-210deg);
}

input:focus~ul li:nth-child(3)::after,
input:valid~ul li:nth-child(3)::after {
    -webkit-filter: blur(0);
            filter: blur(0);
    -webkit-transform: scale(1) rotate(0);
            transform: scale(1) rotate(0);
}

input:focus~ul li:nth-child(4)::before,
input:valid~ul li:nth-child(4)::before {
    -webkit-filter: blur(20px);
            filter: blur(20px);
    -webkit-transform: translate(0, -35px) rotate(-210deg);
            transform: translate(0, -35px) rotate(-210deg);
}

input:focus~ul li:nth-child(4)::after,
input:valid~ul li:nth-child(4)::after {
    -webkit-filter: blur(0);
            filter: blur(0);
    -webkit-transform: scale(1) rotate(0);
            transform: scale(1) rotate(0);
}

input:focus~ul li:nth-child(5)::before,
input:valid~ul li:nth-child(5)::before {
    -webkit-filter: blur(20px);
            filter: blur(20px);
    -webkit-transform: translate(0, -35px) rotate(-210deg);
            transform: translate(0, -35px) rotate(-210deg);
}

input:focus~ul li:nth-child(5)::after,
input:valid~ul li:nth-child(5)::after {
    -webkit-filter: blur(0);
            filter: blur(0);
    -webkit-transform: scale(1) rotate(0);
            transform: scale(1) rotate(0);
}

input:focus~ul li:nth-child(6)::before,
input:valid~ul li:nth-child(6)::before {
    -webkit-filter: blur(20px);
            filter: blur(20px);
    -webkit-transform: translate(0, -35px) rotate(-210deg);
            transform: translate(0, -35px) rotate(-210deg);
}

input:focus~ul li:nth-child(6)::after,
input:valid~ul li:nth-child(6)::after {
    -webkit-filter: blur(0);
            filter: blur(0);
    -webkit-transform: scale(1) rotate(0);
            transform: scale(1) rotate(0);
}

input:focus~ul li:nth-child(1)::after,
input:valid~ul li:nth-child(1)::after {
    transition-delay: 400ms;
}

input:focus~ul li:nth-child(2)::after,
input:valid~ul li:nth-child(2)::after {
    transition-delay: 600ms;
}

input:focus~ul li:nth-child(3)::after,
input:valid~ul li:nth-child(3)::after {
    transition-delay: 800ms;
}

input:focus~ul li:nth-child(4)::after,
