    transition-delay: 1200ms;
}

input:focus~ul li:nth-child(6)::after,
input:valid~ul li:nth-child(6)::after {
    transition-delay: 1400ms;
}

label {
    position: relative;
}

ul {
    position: absolute;
    top: 38px;
    left: -4px;
    margin: 0;
    list-style: none;
    padding-left: 22px;
    position: absolute;
    display: flex;
    pointer-events: none;
    transition: 200ms;
}

ul li {
    letter-spacing: 3px;
    text-transform: uppercase;
    color: transparent;
    pointer-events: none;
    transition: 500ms ease-in-out;
}

ul li ::selection {
    color: transparent;
}

ul li::after,
ul li::before {
    color: black;
    position: absolute;
    transition: 500ms ease-in-out;
}

ul li::before {
    top: -20px;
}

ul li::after {
    top: -63px;
    -webkit-transform: scale(0) rotate(210deg);
            transform: scale(0) rotate(210deg);
    -webkit-filter: blur(20px);
            filter: blur(20px);
}

ul li[s]::before {
    content: attr(s) "s";
    transition-delay: 200ms;
}

ul li[s]::after {
    content: attr(s) "s";
    left: 12px;
    color: #666;
}

ul li[e]::before {
    content: attr(e) "e";
    transition-delay: 400ms;
}

ul li[e]::after {
    content: attr(e) "e";
    left: 24px;
    color: #666;
}

ul li[a]::before {
    content: attr(a) "a";
    transition-delay: 600ms;
}

ul li[a]::after {
    content: attr(a) "a";
    left: 36px;
    color: #666;
}

ul li[r]::before {
    content: attr(r) "r";
    transition-delay: 800ms;
}

ul li[r]::after {
    content: attr(r) "r";
    left: 48px;
    color: #666;
}

ul li[c]::before {
    content: attr(c) "c";
    transition-delay: 1000ms;
}

ul li[c]::after {
    content: attr(c) "c";
    left: 60px;
    color: #666;
}

ul li[h]::before {
    content: attr(h) "h";
    transition-delay: 1200ms;
}

ul li[h]::after {
    content: attr(h) "h";
    left: 72px;
    color: #666;
}
.popup {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100vh;
    background-color: rgba(0, 0, 0, 0.2);

    display: flex;
    justify-content: center;
    align-items: center;
    z-index: 10;
}

.popup-inner {
    position: relative;
    padding: 32px;
    width: 100%;
    max-width: 640px;
    background-color: #fff;
}

.popup-inner .close-btn {
    position: absolute;
    top: 16px;
    right: 16px;
}

.close-btn {
    margin-bottom: 15px;
}
